"use client";

import React, { useState, useEffect, useRef } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
    Eye, Monitor, Wifi, Radio, Globe, Shield,
    Terminal, Play, Square, Cpu, Command, Activity, Zap, Users, Target, Maximize2, Minimize2
} from 'lucide-react';

const MOCK_LOGS = [
    { time: "10:00:01", level: "INFO", message: "System initialized. Waiting for command..." }
];

export default function LiveViewPage() {
    const [runningAgent, setRunningAgent] = useState<string | null>(null);
    const [logs, setLogs] = useState(MOCK_LOGS);
    const [isExpanded, setIsExpanded] = useState(false);
    const scrollRef = useRef<HTMLDivElement>(null);
    const wsRef = useRef<WebSocket | null>(null);
    const [screenshot, setScreenshot] = useState<string | null>(null);

    useEffect(() => {
        // Connect to WebSocket on port 4000 for live streaming
        const wsUrl = `ws://localhost:4000/ws/stream`;

        console.log("Connecting to surveillance stream:", wsUrl);
        wsRef.current = new WebSocket(wsUrl);

        wsRef.current.onmessage = (event) => {
            try {
                const data = JSON.parse(event.data);
                if (data.type === 'log') {
                    addLog(data.level?.toUpperCase() || 'INFO', data.message);
                } else if (data.type === 'screenshot') {
                    setScreenshot(`data:image/jpeg;base64,${data.image}`);
                }
            } catch (e) {
                console.error("Surveillance Data Error", e);
            }
        };

        return () => {
            wsRef.current?.close();
        };
    }, []);

    useEffect(() => {
        if (scrollRef.current) {
            scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
        }
    }, [logs]);

    const startAgent = async (agentName: string, endpoint: string) => {
        if (runningAgent) return;

        setRunningAgent(agentName);
        addLog("INFO", `Initializing protocols for ${agentName}...`);

        try {
            const res = await fetch(`http://localhost:4000/api/agents/start/${endpoint}`, { method: 'POST' });
            const data = await res.json();

            if (data.status === 'ok') {
                addLog("SUCCESS", `${agentName} core online. Stream synchronized.`);
            } else {
                addLog("ERROR", `Protocol breach: ${data.message}`);
                setRunningAgent(null);
            }
        } catch (err) {
            addLog("ERROR", "Uplink Failed. Verify backend port 4000.");
            setRunningAgent(null);
        }
    };

    const stopAgent = async () => {
        if (!runningAgent) return;

        addLog("WARN", "Injecting termination signal...");
        try {
            await fetch('http://localhost:4000/api/agents/stop-all', { method: 'POST' });
            addLog("INFO", "Operation scrubbed by operator.");
            setRunningAgent(null);
            setScreenshot(null);
        } catch (err) {
            addLog("ERROR", "Termination failed. Manual intervention required.");
        }
    };

    const addLog = (level: string, message: string) => {
        setLogs(prev => [...prev.slice(-100), {
            time: new Date().toLocaleTimeString(),
            level,
            message
        }]);
    };

    return (
        <div className="min-h-screen p-6 space-y-6 pb-20 relative overflow-hidden bg-[#02040a]">
            {/* Background Ambient Effects */}
            <div className="absolute top-0 left-0 w-full h-full pointer-events-none opacity-20">
                <div className="absolute top-[-10%] right-[-10%] w-[50%] h-[50%] bg-blue-600/20 blur-[120px] rounded-full" />
                <div className="absolute bottom-[-10%] left-[-10%] w-[50%] h-[50%] bg-purple-600/10 blur-[120px] rounded-full" />
            </div>

            {/* Header HUD */}
            <div className="flex flex-col md:flex-row justify-between items-end gap-4 border-b border-white/5 pb-6 relative z-10">
                <div>
                    <h1 className="text-3xl font-black bg-clip-text text-transparent bg-linear-to-r from-blue-400 via-purple-400 to-pink-400 uppercase italic tracking-tighter">
                        MISSION <span className="text-white">CONTROL</span>
                    </h1>
                    <p className="text-[10px] text-gray-500 font-bold uppercase tracking-[0.4em] mt-2 flex items-center gap-2">
                        <Activity size={12} className="text-blue-500" />
                        Live Surveillance Stream // Encrypted
                    </p>
                </div>
                <div className="flex items-center gap-4 bg-white/5 px-4 py-2 rounded-xl border border-white/5 backdrop-blur-md">
                    <div className="flex flex-col items-end">
                        <span className="text-[8px] text-gray-500 font-bold uppercase tracking-widest">Uplink Status</span>
                        <span className="text-[10px] font-black text-green-400 uppercase tracking-tighter italic flex items-center gap-2">
                            <div className="w-1.5 h-1.5 rounded-full bg-green-500 animate-pulse" />
                            Socket Connected
                        </span>
                    </div>
                    <div className="w-px h-8 bg-white/10" />
                    <div className="flex flex-col items-end">
                        <span className="text-[8px] text-gray-500 font-bold uppercase tracking-widest">Global Latency</span>
                        <span className="text-[10px] font-black text-white italic">12ms</span>
                    </div>
                </div>
            </div>

            <div className={`grid gap-6 h-[calc(100vh-220px)] transition-all duration-500 relative z-10 ${isExpanded ? 'grid-cols-1' : 'grid-cols-1 lg:grid-cols-4'}`}>

                {/* LEFT: Tactics Hub (Hidden when expanded) */}
                {!isExpanded && (
                    <motion.div
                        initial={{ opacity: 0, x: -50 }} animate={{ opacity: 1, x: 0 }}
                        className="glass-card p-6 rounded-2xl border border-white/5 flex flex-col gap-6 lg:col-span-1 bg-black/40 backdrop-blur-xl"
                    >
                        <div className="flex flex-col gap-1">
                            <h2 className="text-xs font-black text-gray-100 uppercase tracking-[0.3em] flex items-center gap-2 italic">
                                <Cpu size={16} className="text-blue-400" />
                                Tactics Hub
                            </h2>
                            <div className="h-0.5 w-12 bg-blue-500 rounded-full mt-1" />
                        </div>

                        {/* Tactical Status */}
                        <div className={`p-4 rounded-xl border transition-all duration-500 ${runningAgent
                            ? 'bg-blue-500/10 border-blue-500/30 shadow-[0_0_20px_rgba(59,130,246,0.1)]'
                            : 'bg-white/5 border-white/10'}`}>
                            <div className="flex justify-between items-center mb-1">
                                <span className="text-xs font-bold text-gray-500 uppercase tracking-widest">Operation Status</span>
                                <span className={`text-[10px] font-black px-2 py-0.5 rounded-md ${runningAgent ? 'text-blue-400' : 'text-gray-500'}`}>
                                    {runningAgent ? 'ENGAGED' : 'READIED'}
                                </span>
                            </div>
                            <div className="h-1 bg-white/5 rounded-full overflow-hidden mt-3">
                                <motion.div
                                    initial={{ width: 0 }}
                                    animate={{ width: runningAgent ? '100%' : '0%' }}
                                    className="h-full bg-blue-500 shadow-[0_0_10px_rgba(59,130,246,0.5)]"
                                />
                            </div>
                        </div>

                        {/* Emergency Controls */}
                        <AnimatePresence>
                            {runningAgent && (
                                <motion.button
                                    initial={{ opacity: 0, scale: 0.95 }} animate={{ opacity: 1, scale: 1 }} exit={{ opacity: 0, scale: 0.95 }}
                                    onClick={stopAgent}
                                    className="w-full py-4 rounded-xl font-black text-[10px] uppercase tracking-[0.3em] text-red-400 bg-red-500/10 hover:bg-red-500/20 border border-red-500/30 flex items-center justify-center gap-3 transition-all italic shadow-lg shadow-red-500/5 group"
                                >
                                    <Square fill="currentColor" size={14} className="group-hover:scale-125 transition-transform" />
                                    Abort Operation
                                </motion.button>
                            )}
                        </AnimatePresence>

                        {/* Agent Selection */}
                        <div className="flex flex-col gap-4 overflow-y-auto scrollbar-hide flex-1 pr-1">
                            <p className="text-[10px] font-black text-gray-500 uppercase tracking-[0.3em] italic">Protocol Selection</p>

                            <AgentButton
                                title="Feed Warmer"
                                desc="Engagement Protocol"
                                icon={Zap}
                                color="blue"
                                onClick={() => startAgent("Feed Warmer", "feedWarmer")}
                                disabled={!!runningAgent}
                            />

                            <AgentButton
                                title="Growth Manager"
                                desc="Connection Loop"
                                icon={Target}
                                color="purple"
                                onClick={() => startAgent("Growth Manager", "growthManager")}
                                disabled={!!runningAgent}
                            />

                            <AgentButton
                                title="CRM Extraction"
                                desc="Intelligence Sync"
                                icon={Users}
                                color="pink"
                                onClick={() => startAgent("CRM Extraction", "leadCampaign")}
                                disabled={!!runningAgent}
                            />

                            <AgentButton
                                title="Audit Agent"
                                desc="Verifying Node State"
                                icon={Globe}
                                color="gray"
                                onClick={() => startAgent("Audit Agent", "connectionChecker")}
                                disabled={!!runningAgent}
                            />
                        </div>
                    </motion.div>
                )}

                {/* RIGHT: Visual Surveillance (Takes 75% width normally, 100% if expanded) */}
                <div className={`flex flex-col gap-6 ${isExpanded ? 'col-span-1' : 'lg:col-span-3'}`}>

                    {/* Viewport HUD */}
                    <motion.div
                        layout
                        className="flex-1 glass-card rounded-2xl border border-white/5 flex flex-col overflow-hidden relative group min-h-[500px] bg-black shadow-2xl"
                    >
                        {/* CRT Effect Overlay */}
                        <div className="absolute inset-0 pointer-events-none z-20 overflow-hidden opacity-30 select-none">
                            <div className="absolute inset-0 bg-[linear-gradient(rgba(18,16,16,0)_50%,rgba(0,0,0,0.25)_50%),linear-gradient(90deg,rgba(255,0,0,0.06),rgba(0,255,0,0.02),rgba(0,0,255,0.06))] bg-[length:100%_2px,3px_100%]" />
                            <div className="absolute inset-0 animate-pulse bg-white/2" />
                        </div>

                        {/* Viewport Toolbar */}
                        <div className="h-12 bg-white/5 border-b border-white/10 flex items-center px-6 gap-6 justify-between relative z-30">
                            <div className="flex items-center gap-6 flex-1">
                                <div className="flex gap-2">
                                    <div className="w-2.5 h-2.5 rounded-full bg-red-500/30" />
                                    <div className="w-2.5 h-2.5 rounded-full bg-yellow-500/30" />
                                    <div className="w-2.5 h-2.5 rounded-full bg-green-500/30" />
                                </div>
                                <div className="flex-1 max-w-2xl bg-black/40 rounded-lg h-7 flex items-center px-4 text-[10px] text-gray-400 font-mono tracking-wider border border-white/5">
                                    <Shield size={10} className="mr-3 text-blue-500 opacity-70" />
                                    SURVEILLANCE_CHANNEL://LINKEDIN.COM/FEED
                                </div>
                            </div>
                            <button
                                onClick={() => setIsExpanded(!isExpanded)}
                                className="w-8 h-8 rounded-lg bg-white/5 hover:bg-white/10 flex items-center justify-center transition-all border border-white/5"
                            >
                                {isExpanded ? <Minimize2 size={14} className="text-gray-400" /> : <Maximize2 size={14} className="text-gray-400" />}
                            </button>
                        </div>

                        {/* Viewport Content */}
                        <div className="flex-1 bg-[#02040a] relative flex items-center justify-center overflow-hidden">
                            {screenshot ? (
                                <motion.div
                                    initial={{ opacity: 0, scale: 0.98 }}
                                    animate={{ opacity: 1, scale: 1 }}
                                    className="w-full h-full relative"
                                >
                                    <img
                                        src={screenshot}
                                        alt="Surveillance Feed"
                                        className="w-full h-full object-contain"
                                    />
                                    {/* HUD Elements over video */}
                                    <div className="absolute top-6 left-6 flex flex-col gap-2">
                                        <div className="bg-black/60 backdrop-blur-md px-3 py-1.5 rounded border border-white/10 text-[9px] font-mono text-gray-300">
                                            REC_01: {new Date().toLocaleTimeString()}
                                        </div>
                                        <div className="bg-blue-600/60 backdrop-blur-md px-3 py-1.5 rounded border border-blue-400/30 text-[9px] font-mono text-white flex items-center gap-2">
                                            <div className="w-1.5 h-1.5 bg-white rounded-full animate-ping" />
                                            CHANNEL_ALPHA
                                        </div>
                                    </div>
                                </motion.div>
                            ) : (
                                <div className="text-center space-y-6 z-10">
                                    {runningAgent ? (
                                        <div className="flex flex-col items-center gap-6">
                                            <div className="relative">
                                                <div className="w-20 h-20 border-2 border-blue-500/20 rounded-full" />
                                                <div className="absolute inset-0 w-20 h-20 border-2 border-blue-500 border-t-transparent rounded-full animate-spin" />
                                                <Radio size={24} className="absolute inset-0 m-auto text-blue-500 animate-pulse" />
                                            </div>
                                            <div className="space-y-2">
                                                <p className="text-blue-400 font-black text-xs uppercase tracking-[0.4em] italic">Synchronizing Uplink</p>
                                                <p className="text-gray-600 font-mono text-[9px] uppercase tracking-widest">Bridging secure browser stream...</p>
                                            </div>
                                        </div>
                                    ) : (
                                        <div className="flex flex-col items-center gap-4 opacity-20">
                                            <Monitor size={80} className="text-gray-500" />
                                            <p className="text-gray-500 font-black text-xs uppercase tracking-[0.5em] italic">Surveillance Hub Offline</p>
                                        </div>
                                    )}
                                </div>
                            )}

                            {/* Corner Accents */}
                            <div className="absolute top-0 left-0 w-16 h-16 border-t-2 border-l-2 border-white/10 pointer-events-none" />
                            <div className="absolute top-0 right-0 w-16 h-16 border-t-2 border-r-2 border-white/10 pointer-events-none" />
                            <div className="absolute bottom-0 left-0 w-16 h-16 border-b-2 border-l-2 border-white/10 pointer-events-none" />
                            <div className="absolute bottom-0 right-0 w-16 h-16 border-b-2 border-r-2 border-white/10 pointer-events-none" />
                        </div>
                    </motion.div>

                    {/* Operational Terminal */}
                    <motion.div
                        className="h-56 glass-card rounded-2xl border border-white/5 flex flex-col overflow-hidden bg-black/60 backdrop-blur-md"
                    >
                        <div className="px-6 py-3 border-b border-white/10 bg-white/5 flex items-center justify-between">
                            <div className="flex items-center gap-3">
                                <Terminal size={14} className="text-blue-400" />
                                <span className="text-[10px] font-black text-gray-400 uppercase tracking-widest italic">System Execution Log</span>
                            </div>
                            <div className="flex gap-2">
                                <div className="w-2 h-2 rounded-full bg-blue-500/20" />
                                <div className="w-2 h-2 rounded-full bg-blue-500/20" />
                            </div>
                        </div>

                        <div ref={scrollRef} className="flex-1 p-6 font-mono text-[10px] overflow-y-auto scrollbar-hide space-y-2">
                            {logs.map((log, i) => (
                                <div key={i} className="flex gap-4 group transition-colors hover:bg-white/5 p-1 rounded">
                                    <span className="text-gray-600 shrink-0 select-none">[{log.time}]</span>
                                    <span className={`shrink-0 font-black w-16 italic ${log.level === 'INFO' ? 'text-blue-400' :
                                        log.level === 'WARN' ? 'text-yellow-400' :
                                            log.level === 'ERROR' ? 'text-red-400' :
                                                'text-green-400'
                                        }`}>
                                        {log.level}
                                    </span>
                                    <span className="text-gray-400 group-hover:text-gray-200 transition-colors">{log.message}</span>
                                </div>
                            ))}
                        </div>
                    </motion.div>
                </div>
            </div>
        </div>
    );
}

function AgentButton({ title, desc, icon: Icon, color, onClick, disabled }: any) {
    const colorStyles: any = {
        blue: "text-blue-400 border-blue-500/20 hover:border-blue-500/50 bg-blue-500/5 shadow-blue-500/5",
        purple: "text-purple-400 border-purple-500/20 hover:border-purple-500/50 bg-purple-500/5 shadow-purple-500/5",
        pink: "text-pink-400 border-pink-500/20 hover:border-pink-500/50 bg-pink-500/5 shadow-pink-500/5",
        gray: "text-gray-400 border-gray-500/20 hover:border-gray-500/50 bg-gray-500/5 shadow-gray-500/5",
    };

    return (
        <button
            onClick={onClick}
            disabled={disabled}
            className={`flex items-center gap-4 p-4 rounded-xl border text-left transition-all hover:translate-x-1 group disabled:opacity-30 disabled:cursor-not-allowed ${colorStyles[color]} hover:bg-black/40`}
        >
            <div className={`w-10 h-10 rounded-lg flex items-center justify-center bg-white/5 border border-white/5 group-hover:scale-110 transition-transform ${colorStyles[color]}`}>
                <Icon size={18} />
            </div>
            <div className="min-w-0">
                <h3 className="font-black text-gray-200 text-xs uppercase tracking-widest italic group-hover:text-white truncate">{title}</h3>
                <p className="text-[10px] text-gray-500 truncate mt-0.5 font-bold">{desc}</p>
            </div>
        </button>
    )
}
